import React from 'react';
import { CSVViewType } from '../../types';
import EntityForm from 'modules/entity/forms';
import { useAppState } from 'shared/state';
import { useNotistack, useRouter } from 'shared/hooks';
import { KntInputField } from 'shared/components/Fields';
import { Field } from 'formik';
import { Button, Loader } from 'shared/components';

type Props = {
	csvEmployee: CSVViewType;
	onSuccess: () => void;
};

export function EditCsvEmployee({ csvEmployee, onSuccess }: Props) {
	const { companyId } = useAppState();
	const { params } = useRouter();
	const csvId = parseInt((params as { csvId: string })?.csvId);
	const { showNotification } = useNotistack();

	return (
		<div>
			<EntityForm.Main
				entity="csvEmployees"
				name="csvEmployees"
				url={`/${companyId}/my/company/imports/${csvId}/data/${csvEmployee?.id}/`}
				method="patch"
				primaryKey="id"
				sendAsFormData={false}
				normalizeData={(data: any) => data}
				updateData
				fields={[
					{
						name: 'first_name',
						required: true,
						value: csvEmployee?.first_name || '',
						onSubmitKey: ''
					},
					{
						name: 'last_name',
						required: true,
						value: csvEmployee?.last_name || '',
						onSubmitKey: ''
					},
					{
						name: 'phone',
						required: true,
						value: csvEmployee?.phone || '',
						onSubmitKey: ''
					},
					{
						name: 'email',
						required: true,
						value: csvEmployee?.email || '',
						onSubmitKey: ''
					},
					{
						name: 'role',
						required: true,
						value: csvEmployee?.role || '',
						onSubmitKey: ''
					}
				]}
				onSuccess={(response, resetForm): any => {
					showNotification({ message: 'Successfully Edited!' });
					onSuccess();
					resetForm();
				}}
				onError={(error, resetForm): any => {
					const errorMessageText = error?.response?.data?.message || 'Something went wrong';
					showNotification({
						message: errorMessageText,
						variant: 'error'
					});
				}}
			>
				{({ values, isSubmitting }) => {
					if (process.env.NODE_ENV === 'development') {
						console.log('values edit csv ', values);
					}
					return (
						<>
							{isSubmitting && <Loader tiled withOverlay />}
							<h2 className="mb-4 text-lg font-bold">Edit Employee</h2>
							<Field
								label={'First Name'}
								labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
								name="first_name"
								component={KntInputField}
								placeholder={'Type your first name'}
								required
							/>
							<Field
								label={'Last Name'}
								labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
								name="last_name"
								component={KntInputField}
								placeholder={'Type your last name'}
								required
							/>
							<Field
								label={'Email'}
								labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
								name="email"
								component={KntInputField}
								placeholder={'Type your email'}
								required
							/>
							<Field
								label={'Phone'}
								labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
								name="phone"
								component={KntInputField}
								placeholder={'Type your phone'}
								required
							/>
							<Field
								label={'Role'}
								labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
								name="role"
								component={KntInputField}
								placeholder={'Type your role'}
								required
							/>
							<div className="flex-center-end">
								<Button type="button" onClickHandler={onSuccess}>
									Cancel
								</Button>
								<Button
									variant="contained"
									className="ml-4 hover:!text-primary"
									type="submit"
									disabled={isSubmitting}
								>
									Update
								</Button>
							</div>
						</>
					);
				}}
			</EntityForm.Main>
		</div>
	);
}
