import React from 'react';
import EntityContainer from 'modules/entity/containers';
import {
	Pagination,
	Skeleton,
	TableBody,
	TableContainer,
	TableHead,
	WhiteModal
} from 'shared/components/Elements';
import {
	ActionsButtons,
	ConfirmModal,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { useAppState } from 'shared/state';
import { usePagination, useRouter } from 'shared/hooks';
import { useDebounce } from 'use-debounce';
import { useCsvView } from '../../hooks';
import { useCSVManagementContext } from '../../contexts';
import { CSVType, CSVViewType } from '../../types';
import { Link } from 'react-router-dom';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import Typography from '@material-ui/core/Typography';
import { EditCsvEmployee } from './EditCsvEmployee';

export function CSVDetails() {
	const { companyId } = useAppState();
	const { page, handlePage } = usePagination();
	const { searchQuery: query } = useCSVManagementContext();
	const { params } = useRouter();
	const csvId = parseInt((params as { csvId: string })?.csvId);

	const {
		deleteLoading,
		handleClientDelete,
		showDeleteVisible,
		closeDeletePopup,
		deleteVisible,
		editVisible,
		showEditVisible,
		closeEditPopup
	} = useCsvView();
	const [searchQuery] = useDebounce<string>(query, 500);

	const deleteClientHandler = () => () => handleClientDelete(deleteVisible!);

	return (
		<div>
			<EntityContainer.One
				id={csvId}
				relations={''}
				entity="csv-list"
				name="Csv"
				url={`/${companyId}/my/company/imports/${csvId}/`}
				params={{}}
			>
				{({ item, isFetched: isFetchedOne }) => {
					return (
						<>
							<div className="news-breadcrumb mb-5">
								{isFetchedOne ? (
									<Breadcrumbs aria-label="breadcrumb">
										<Link to="/csv">
											<span className="hover:text-kgrey">All CSV files</span>
										</Link>
										<Typography color="textPrimary">{(item as CSVType).file_name}</Typography>
									</Breadcrumbs>
								) : (
									<div className="w-[200px]">
										<Skeleton />
									</div>
								)}
							</div>
							{companyId ? (
								<EntityContainer.All
									entity="csvEmployees"
									name="AllCsvEmployees"
									url={`/${companyId}/my/company/imports/${csvId}/data/`}
									params={{
										limit: 10,
										extra: {
											search: searchQuery
										},
										page
									}}
								>
									{({ items, isFetched, meta }) => {
										const csvViewItems = items as CSVViewType[];
										return (
											<div>
												<TableContainer classes={{ root: 'relative ' }}>
													<StyledTable
														aria-label="simple table"
														classes={{ root: 'table-fixed dark-mode-styled-table' }}
													>
														<TableHead>
															<StyledTableRow>
																<StyledTableHeadCell style={{ width: '16%' }}>
																	Full Name
																</StyledTableHeadCell>
																<StyledTableHeadCell style={{ width: '20%' }}>
																	Email
																</StyledTableHeadCell>
																<StyledTableHeadCell style={{ width: '10%' }}>
																	Phone
																</StyledTableHeadCell>
																<StyledTableHeadCell style={{ width: '7%' }}>
																	Position
																</StyledTableHeadCell>
																<StyledTableHeadCell style={{ width: '7%' }}>
																	Status
																</StyledTableHeadCell>
																<StyledTableHeadCell style={{ width: '10%' }}>
																	Company Name
																</StyledTableHeadCell>
																<StyledTableHeadCell style={{ width: '15%' }}>
																	Description
																</StyledTableHeadCell>
																<StyledTableHeadCell style={{ width: '15%' }}>
																	Actions
																</StyledTableHeadCell>
															</StyledTableRow>
														</TableHead>
														<TableBody>
															{csvViewItems.map((item) => {
																const {
																	id,
																	company_name,
																	description,
																	email,
																	first_name,
																	last_name,
																	phone,
																	role,
																	status
																} = item;
																const fullName = `${first_name} ${last_name}`;
																return (
																	<StyledTableRow key={id}>
																		<StyledTableCell>
																			<div title={fullName}>{fullName}</div>
																		</StyledTableCell>
																		<StyledTableCell className="truncate" title={email}>
																			{email}
																		</StyledTableCell>
																		<StyledTableCell>
																			<div className=" truncate" title={phone}>
																				{phone}
																			</div>
																		</StyledTableCell>
																		<StyledTableCell>
																			<div className=" truncate" title={role}>
																				{role}
																			</div>
																		</StyledTableCell>
																		<StyledTableCell>
																			<div className=" truncate" title={status ?? ''}>
																				{status}
																			</div>
																		</StyledTableCell>
																		<StyledTableCell>
																			<div className=" truncate" title={company_name}>
																				{company_name}
																			</div>
																		</StyledTableCell>
																		<StyledTableCell>
																			<div className=" truncate" title={description ?? ''}>
																				{description}
																			</div>
																		</StyledTableCell>
																		<StyledTableCell>
																			<div className="flex items-center justify-end ">
																				<ActionsButtons.DeleteButton
																					clickHandler={() => showDeleteVisible(item)}
																				/>
																				<ActionsButtons.EditButton
																					clickHandler={() => showEditVisible(item)}
																				/>
																			</div>
																		</StyledTableCell>
																	</StyledTableRow>
																);
															})}
														</TableBody>
													</StyledTable>
													{!isFetched && <Loader tiled withOverlay />}
												</TableContainer>
												<ConfirmModal
													featureHandler={deleteClientHandler()}
													isOpen={Boolean(deleteVisible)}
													toggleHandler={closeDeletePopup}
													title={`Are you sure delete ${deleteVisible?.first_name} ${deleteVisible?.last_name}?`}
													hasLoader
													isLoading={deleteLoading}
												/>
												<WhiteModal handleModalOpen={closeEditPopup} open={Boolean(editVisible)}>
													<EditCsvEmployee csvEmployee={editVisible!} onSuccess={closeEditPopup} />
												</WhiteModal>
												<div className="news__pagination my-4">
													{items.length ? (
														<Pagination
															count={meta?.last_page}
															page={page ? +page : 1}
															defaultPage={6}
															siblingCount={1}
															color={'primary'}
															onChange={handlePage}
														/>
													) : null}
												</div>
											</div>
										);
									}}
								</EntityContainer.All>
							) : (
								<div className="flex justify-center">
									<Loader />
								</div>
							)}
						</>
					);
				}}
			</EntityContainer.One>
		</div>
	);
}
